@font-face {
    font-family:'Turkishye';
    src: url('/fonts/Turkishye.eot');
	src: url('/fonts/Turkishye.eot?#iefix') format('embedded-opentype'),
		url('/fonts/Turkishye.woff2') format('woff2'),
		url('/fonts/Turkishye.woff') format('woff'),
		url('/fonts/Turkishye.svg#Turkishye') format('svg');
    font-weight: 400;
    font-style: normal;
    font-stretch: normal;
    unicode-range: U+0020-00FE;
}
